
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'DialogModal',
props: {
isShow: {
type: Boolean,
default: false,
required: true
},
isSkip: {
type: Boolean,
default: false,
required: true
},
isTransfer: {
type: Boolean,
default: true,
required: false
},
modalTitle: {
type: String,
default: '',
required: true
},
warningText: {
type: String,
default: '',
required: true
},
messageText: {
required: true
}
},
components: {
XButton
},
data () {
return {
seed:new Date().getTime()
}
},
computed: {
application() {
return "data:application/json," + encodeURIComponent(this.messageText)
}
},
methods: {
closeMyself() {
this.$emit("on-close")
},
handleSuccess (e) {
var that = this
if(e.text) {
this.$vux.toast.show({
type: 'success',
text: '复制成功',
})

that.closeMyself()
if (that.isSkip) {
setTimeout(() => {
that.$router.push({ path: '/user' })
}, 2000)
}
}
}
}
}



